import React from 'react'
import PropTypes from 'prop-types'
import { Button, createMuiTheme, withStyles } from '@material-ui/core'
import { Phone as PhoneIcon } from '@material-ui/icons'
import ThemeProvider from '@material-ui/styles/ThemeProvider/ThemeProvider'
import { graphql, StaticQuery } from 'gatsby'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#47B947',
    },
  },
})

const styles = () => ({
  button: {
    color: '#FFFFFF',
  },
})

const CtaButton = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query ContactData {
          allContactJson {
            nodes {
              telephone
            }
          }
        }
      `}
      render={data => {
        const { allContactJson } = data
        const { telephone } = allContactJson.nodes[0]

        const callTelephone = number => {
          window.location = 'tel:' + number
        }

        return (
          <ThemeProvider theme={theme}>
            <Button
              variant={'contained'}
              size={'large'}
              color={'primary'}
              className={classes.button}
              onClick={() => callTelephone(telephone)}
            >
              <PhoneIcon />
              {telephone}
            </Button>
          </ThemeProvider>
        )
      }}
    />
  )
}

CtaButton.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(CtaButton)
