import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import { Container, Grid, Typography, withStyles } from '@material-ui/core'

import SectionHeader from '../../components/sections-header/section-header'
import ServiceItem from '../../components/service-item/service-item'

const styles = () => ({
  root: {
    paddingTop: '3rem',
  },
  item: { align: 'center' },
})

const Services = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query Services {
          allServicesJson {
            nodes {
              label
              image
              desc
            }
          }
          allSectionsJson(filter: { id: { eq: "services" } }) {
            nodes {
              title
              desc
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allServicesJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const services = allServicesJson.nodes

        return (
          <Container>
            <SectionHeader title={sectionDetails.title} />
            <Typography variant={'body1'} align={'center'}>
              {sectionDetails.desc}
            </Typography>
            <Grid container justify={'center'} className={classes.root}>
              {services.map(service => (
                <Grid item md={4} className={classes.item}>
                  <ServiceItem
                    label={service.label}
                    desc={service.desc}
                    image={service.image}
                  />
                </Grid>
              ))}
            </Grid>
          </Container>
        )
      }}
    />
  )
}

export default withStyles(styles)(Services)
