import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import { Container, Grid, withStyles } from '@material-ui/core'

import SectionHeader from '../../components/sections-header/section-header'
import ProfileCard from '../../components/profile-card/profile-card'

const styles = () => ({
  root: {},
  child: {},
})

// TODO: merge team json files into a single one for order
const AboutUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query TeamMembers {
          allTeamMembersJson {
            nodes {
              name
              image
              roles
              skills
            }
          }
        }
      `}
      render={data => {
        const { allTeamMembersJson } = data
        const teamMembers = allTeamMembersJson.nodes

        return (
          <Container>
            <SectionHeader title={'About Us'} />
            <Grid container justify={'center'} className={classes.root}>
              {teamMembers.map(teamMember => (
                <Grid item md={6} className={classes.child}>
                  <ProfileCard
                    name={teamMember.name}
                    photo={teamMember.image}
                    roles={teamMember.roles}
                    skills={teamMember.skills}
                  />
                </Grid>
              ))}
            </Grid>
          </Container>
        )
      }}
    />
  )
}

export default withStyles(styles)(AboutUs)
