import React from 'react'
import PropTypes from 'prop-types'
import {
  Card,
  CardContent,
  Chip,
  Container,
  Grid,
  List,
  ListItem,
  ListItemText,
  Typography,
  withWidth,
} from '@material-ui/core'
import { isWidthDown } from '@material-ui/core/withWidth'

import technologies from '../../services/technologies'
import capitalize from '../../services/capitalize'

const TechnologiesCategoryList = ({ categories, callback, minimize }) => {
  let style = {
    textAlign: 'right',
  }
  if (minimize) {
    style.textAlign = 'center'
  }
  return (
    <List dense={true}>
      {categories.map((category, index) => (
        <ListItem button style={style}>
          <ListItemText
            primary={capitalize(category)}
            onMouseEnter={() => callback(category)}
            onMouseLeave={() => callback(null)}
          />
        </ListItem>
      ))}
    </List>
  )
}

TechnologiesCategoryList.propTypes = {
  categories: PropTypes.array.isRequired,
  callback: PropTypes.func.isRequired,
  minimize: PropTypes.bool,
}

const TechnologyList = ({ list, selected }) => {
  return (
    <Grid container spacing={1}>
      {list.map(item => (
        <Grid item>
          <TechnologyListItem
            label={item.label}
            selected={item.categories.indexOf(selected) >= 0}
          />
        </Grid>
      ))}
    </Grid>
  )
}

TechnologyList.propTypes = {
  list: PropTypes.array.isRequired,
  selected: PropTypes.string.isRequired,
}

const TechnologyListItem = ({ label, selected }) => {
  if (selected) {
    return <Chip label={label} color={'primary'} />
  } else {
    return <Chip label={label} />
  }
}

TechnologyListItem.propTypes = {
  label: PropTypes.string.isRequired,
  selected: PropTypes.string.isRequired,
}

const TechnologiesCard = ({ width }) => {
  const possibleTechnologies = technologies.reduce(
    (acc, { categories }) => ({
      ...acc,
      ...categories.reduce((cats, c) => ({ ...cats, [c]: false }), {}),
    }),
    {}
  )

  const categories = Object.keys(possibleTechnologies)

  const [category, setCategory] = React.useState(null)

  return (
    <Container>
      <Card raised={true}>
        <CardContent>
          <Grid container direction={'row'} spacing={2}>
            <Grid item style={{ flexGrow: '1' }} md={3}>
              <Typography variant={'h6'} color={'primary'}>
                Technologien
              </Typography>
              <TechnologiesCategoryList
                categories={categories}
                callback={setCategory}
                minimize={isWidthDown('sm', width)}
              />
            </Grid>
            <Grid item style={{ flexGrow: '2' }} md={9}>
              <TechnologyList list={technologies} selected={category} />
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Container>
  )
}

export default withWidth()(TechnologiesCard)
