import React from 'react'
import {
  Card,
  CardContent,
  CardMedia,
  Container,
  Typography,
  withStyles,
} from '@material-ui/core'
import { graphql, StaticQuery } from 'gatsby'

import Carousel from '../../components/carousel/carousel'
import SectionHeader from '../../components/sections-header/section-header'

const styles = () => ({
  root: {
    flexGrow: 1,
  },
  item: {
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    backgroundSize: 'cover',
    height: '17rem',
  },
})

const SuccessStories = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query CaseStudies {
          allCaseStudiesJson {
            nodes {
              title
              desc
              image
            }
          }
          allSectionsJson(filter: { id: { eq: "case-studies" } }) {
            nodes {
              title
              desc
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allCaseStudiesJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const caseStudies = allCaseStudiesJson.nodes

        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />
            <Typography variant={'body1'} align={'center'}>
              {sectionDetails.desc}
            </Typography>
            <Carousel>
              {caseStudies.map(caseStudy => (
                <Card>
                  <CardMedia
                    className={classes.item}
                    image={caseStudy.image}
                    title={caseStudy.title}
                  />
                  <CardContent>
                    <Typography
                      gutterBottom
                      variant={'h5'}
                      component={'h2'}
                      align={'left'}
                    >
                      {caseStudy.title}
                    </Typography>
                    <Typography
                      gutterBottom
                      variant="body2"
                      component={'p'}
                      align={'justify'}
                    >
                      {caseStudy.desc}
                    </Typography>
                  </CardContent>
                </Card>
              ))}
            </Carousel>
          </Container>
        )
      }}
    />
  )
}

export default withStyles(styles)(SuccessStories)
