import React from 'react'
import PropTypes from 'prop-types'
import SwipeableViews from 'react-swipeable-views'

import { Box, CardContent, Fab, withStyles } from '@material-ui/core'
import { ChevronLeft, ChevronRight } from '@material-ui/icons'
import Card from '@material-ui/core/Card'

const styles = () => ({
  carousel: {
    marginTop: '3rem',
  },
  carouselItem: {
    display: 'inline-flex',
    width: '100%',
    overflowX: 'auto',
    overflowY: 'hidden',
  },
  carouselControls: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  carouselControl: {
    color: '#FFFFFF',
    margin: '1rem'
  },
})

const Carousel = ({ children, classes }) => {
  const [activeStep, setActiveStep] = React.useState(0)
  const count = React.Children.count(children) - 1

  const clickPrev = () => {
    if (activeStep - 1 < 0) {
      setActiveStep(count)
    } else {
      setActiveStep(previousStep => previousStep - 1)
    }
  }

  const clickNext = () => {
    if (activeStep + 1 > count) {
      setActiveStep(0)
    } else {
      setActiveStep(previousStep => previousStep + 1)
    }
  }

  return (
    <Card className={classes.carousel} raised={true}>
      <CardContent>
        <SwipeableViews
          index={activeStep}
          onChangeIndex={setActiveStep}
          enableMouseEvents
          slideClassName={classes.carouselItem}
        >
          {children}
        </SwipeableViews>
        <div className={classes.carouselControls}>
          <Fab
            aria-label="Previous"
            className={classes.carouselControl}
            onClick={() => clickPrev()}
            color={'primary'}
          >
            <ChevronLeft />
          </Fab>
          <Fab
            aria-label="Next"
            className={classes.carouselControl}
            onClick={() => clickNext()}
            color={'primary'}
          >
            <ChevronRight />
          </Fab>
        </div>
      </CardContent>
    </Card>
  )
}

Carousel.propTypes = {
  children: PropTypes.node.isRequired,
}

export default withStyles(styles)(Carousel)
