import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import {
  Container,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Typography,
  withStyles,
} from '@material-ui/core'
import { ChevronRight } from '@material-ui/icons'

import SectionHeader from '../../components/sections-header/section-header'

const styles = () => ({
  root: {
    padding: '2.5rem 1.25rem',
  },
})

const WhyUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query Qualities {
          allQualitiesJson {
            nodes {
              reason
            }
          }
          allSectionsJson(filter: { id: { eq: "qualities" } }) {
            nodes {
              title
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allQualitiesJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const qualities = allQualitiesJson.nodes

        return (
          <Container>
            <SectionHeader title={sectionDetails.title} />
            <List className={classes.root}>
              {qualities.map(({ reason }) => (
                <ListItem dense={true}>
                  <ListItemIcon>
                    <ChevronRight />
                  </ListItemIcon>
                  <ListItemText primary={<Typography variant={'h6'}>{reason}</Typography>} />
                </ListItem>
              ))}
            </List>
          </Container>
        )
      }}
    />
  )
}

export default withStyles(styles)(WhyUs)
