import React from 'react'
import PropTypes from 'prop-types'

import logo from '../../images/compose-us-black.svg'
import { withStyles } from '@material-ui/core'

const styles = () => ({
  root: {
    padding: '1rem 0',
  },
})
const HeroImage = ({ classes }) => {
  return <img src={logo} alt="compose.us Gmbh Logo" className={classes.root} />
}

HeroImage.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(HeroImage)
