import React from 'react'
import PropTypes from 'prop-types'
import {
  Box,
  Card,
  CardContent,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  Typography,
  Tabs,
  Tab,
  withStyles,
} from '@material-ui/core'
import { ChevronRight, Add } from '@material-ui/icons'
import Divider from '@material-ui/core/Divider'

const styles = theme => ({
  root: {
    margin: '3rem;',
  },
  content: {
    // minWidth: '120px',
  },
  actions: {},
  action: {
    backgroundColor: '#47B947',
  },
  photo: {
    maxWidth: '180px',
    maxHeight: '180px',
    borderRadius: '50%',
    border: '0.25rem solid ' + theme.palette.primary.main,
  },
})

const TabPanel = ({ children, value, index, ...other }) => {
  return (
    <Box
      component={'div'}
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {children}
    </Box>
  )
}

TabPanel.propType = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
}

const listItems = (list, icon) => {
  return (
    <List dense={true}>
      {list.map(value => {
        return (
          <ListItem alignItems={'start'}>
            <ListItemIcon>{icon}</ListItemIcon>
            {value}
          </ListItem>
        )
      })}
    </List>
  )
}

const a11yProps = index => {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}

const ProfileCard = ({ classes, photo, name, roles, skills }) => {
  const [value, setValue] = React.useState(0)

  const toggleSection = (event, newValue) => {
    setValue(newValue)
  }

  return (
    <Card className={classes.root}>
      <CardContent className={classes.content}>
        <Grid container xs direction="column">
          <Grid item align={'center'}>
            <img src={photo} className={classes.photo} alt={`Photo: ${name}`} />
            <Typography gutterBottom variant={'h5'} component={'h2'}>
              {name}
            </Typography>
            <Tabs
              value={value}
              onChange={toggleSection}
              variant="fullWidth"
              className={classes.actions}
              aria-label={'full width tabs role skills'}
            >
              <Tab label="Role" {...a11yProps(0)} className={classes.action} />
              <Tab
                label="Skills"
                {...a11yProps(1)}
                className={classes.action}
              />
            </Tabs>
          </Grid>
          <Grid item>
            <TabPanel value={value} index={0}>
              {listItems(roles, <ChevronRight />)}
            </TabPanel>
            <TabPanel value={value} index={1}>
              {listItems(skills, <Add />)}
            </TabPanel>
          </Grid>
          <Divider />
        </Grid>
      </CardContent>
    </Card>
  )
}

ProfileCard.propTypes = {
  classes: PropTypes.object.isRequired,
  photo: PropTypes.element.isRequired,
  name: PropTypes.string.isRequired,
}

export default withStyles(styles)(ProfileCard)
